(function() {
  'use strict';

  angular
    .module('asylumjourneyFrontend', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ngRoute', 'ngDialog']);

})();

(function () {
	'use strict';

	angular
		.module('asylumjourneyFrontend')
		.service('data', data);

	function data($resource) {

		var url = 'http://private-7172d-asylumjourney.apiary-mock.com/';

		this.services = services;
		this.providers = providers;
		this.stages = stages;
		this.categories = categories;
		this.serviceUsers = serviceUsers;
		this.issues = issues;

		///////////

		function services() {
			return $resource(url + 'services');
		}

		function providers() {
			return $resource(url + 'providers');
		}

		function stages() {
			return $resource(url + 'stages');
		}

		function categories() {
			return $resource(url + 'categories');
		}

		function serviceUsers() {
			return $resource(url + 'service-users');
		}

		function issues() {
			return $resource(url + 'issues');
		}
	}
})();

(function() {
  'use strict';

  angular
    .module('asylumjourneyFrontend')
    .directive('overview', overview);

  function overview() {
    var overview = {
      templateUrl: 'app/components/overview/overview.html',
      restrict: 'AE'
    };
    return overview;
  }
})();

(function() {
  'use strict';

  angular
  .module('asylumjourneyFrontend')
  .controller('OverviewController', OverviewController);

    function OverviewController(data) {
        var vm = this;
        vm.services = [];

        activate();

        function activate() {
            getServices();
            getProviders();
            getStages();
            getCategories();
        }

        function getServices() {
            data.services().get().$promise.then(function(response) {
				vm.services = response._embedded.services;
			});
        }

        function getProviders() {
            data.providers().get().$promise.then(function(response) {
				vm.providers = response._embedded.providers;
			});
        }

        function getStages() {
            data.stages().get().$promise.then(function(response) {
				vm.stages = response._embedded.stages;
			});
		}

        function getCategories() {
            data.categories().get().$promise.then(function(response) {
				vm.categories = response._embedded.categories;
			});
		}
    }

})();

(function() {
	'use strict';

	angular
		.module('asylumjourneyFrontend')
		.directive('card', card);

	function card() {
		var card = {
			templateUrl: 'app/components/card/card.html',
			restrict: 'AE',
			controller: 'CardController',
			scope: {
					service: '='
			}
		};
		return card;
	}
})();

(function() {
  'use strict';

  angular
  .module('asylumjourneyFrontend')
  .controller('CardController', CardController);

    function CardController($scope, ngDialog) {
        var vm = this;
        vm.service = $scope.service;

        vm.openDialog = function () {
            ngDialog.open({
                template: 'app/components/detail-overlay/detail.html',
                scope: $scope
            });
        }
    }
    
})();

(function() {
  'use strict';

  angular
    .module('asylumjourneyFrontend')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController() {
  }
})();

(function () {
	'use strict';

	angular
		.module('asylumjourneyFrontend')
		.filter('stage', stage);

	function stage() {

		return function stageFilter(items, stage) {
			var filtered = [];

			angular.forEach(items, function(item) {
				angular.forEach(item._embedded.stages, function(itemStage) {
					if (itemStage.id === stage) {
						filtered.push(item);
					}
				});
			});
			return filtered;
		}
	}
})();

(function () {
	'use strict';

	angular
		.module('asylumjourneyFrontend')
		.filter('category', category);

	function category() {

		return function categoryFilter(items, category) {
			var filtered = [];

			angular.forEach(items, function(item) {
				angular.forEach(item._embedded.categories, function(itemCategory) {
					if (itemCategory.id === category) {
						filtered.push(item);
					}
				});
			});

			return filtered;
		}
	}
})();

(function() {
  'use strict';

  angular
    .module('asylumjourneyFrontend')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('asylumjourneyFrontend')
    .config(routeConfig);

  function routeConfig($routeProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .otherwise({
        redirectTo: '/'
      });
  }

})();

/* global moment:false */
(function() {
  'use strict';

  angular
    .module('asylumjourneyFrontend')
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('asylumjourneyFrontend')
    .config(config);

  /** @ngInject */
  function config($logProvider) {
    // Enable log
    $logProvider.debugEnabled(true);
  }

})();

angular.module("asylumjourneyFrontend").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=\"container\"><overview></overview></div>");
$templateCache.put("app/components/detail-overlay/detail.html","<article class=\"details\"><h2 class=\"details__heading\">{{ service.name }}</h2><p>{{ service.description }}</p><p>{{ service.phone }}</p><a href=\"mailto:{{ service.email }}\">{{ service.email }}</a><p>{{ service.address }} {{ service.postcode }}</p><p><a ng-href=\"{{ service.url }}\">{{ service.url }}</a></p></article>");
$templateCache.put("app/components/card/card.html","<a ng-controller=\"CardController as card\" href=\"#/\" ng-click=\"card.openDialog()\" class=\"card\"><h3>{{ card.service.name }}</h3></a>");
$templateCache.put("app/components/overview/overview.html","<div ng-controller=\"OverviewController as overview\" class=\"overview\"><ul class=\"overview-stages\"><li class=\"overview-stages__stage\"></li><li ng-repeat=\"stage in overview.stages\" class=\"overview-stages__stage overview-stages__stage--{{$index + 1}}\">{{stage.name}}</li></ul><div ng-repeat=\"category in overview.categories\" class=\"overview-category\"><h2 class=\"overview-category__heading\">{{category.name}}</h2><ul class=\"overview-category__stage overview-category__stage--{{stage.id}}\" ng-repeat=\"stage in overview.stages\"><li ng-repeat=\"service in overview.services | category: category.id | stage: stage.id\"><card service=\"service\"></card></li></ul></div></div>");}]);